/* micro reset */

body {
  margin: 0;
  padding: 0;
  background-color: #fff;
}

p {
  opacity: 0.8;
}

/* micro grid*/

.row,
.col {
  display: flex;
}

.row {
  flex-direction: row;
}

.col {
  flex-direction: column;
}

/* styles */

.app {
  width: 100%;
  height: 100%;

  align-items: center;
}

.app__title {
  font-family: 'Roboto', sans-serif;
  font-size: 64px;
}

.wrapper {
  width: 60%;
  justify-content: space-between;
  position: relative;
}

.wrapper > .col {
  width: 30%;
}

.task-wrapper {
  padding: 10px;
}

.btn {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px dashed rgba(0, 0, 0, 0.3);
  background-color: transparent;
  transition: border 0.5s, color 0.5s;
  cursor: pointer;
  outline: none;
}

.col__title {
  font-family: 'Roboto', sans-serif;
}

/* input styles */

.input__task-title,
.input__task-text {
  margin-bottom: 20px;
  border: 1px dashed rgba(0, 0, 0, 0.3);
  padding: 5px;
  outline: none;
}

.input__task-title:focus,
.input__task-text:focus {
  border: 1px dashed #0375b4;
}

.input__task-title {
  font-family: 'Roboto', sans-serif;
  font-size: 32px;
}

.input__task-text {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;

  resize: vertical;
}

/* tasks styles */

.task {
  height: 200px;
  overflow: hidden;
}

.task__wrapper {
  height: 180px;
  margin: 10px 1px;
  padding: 10px;
  border: 1px dashed rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  position: relative;
  transition: border 0.5s;
}

.task__wrapper.done {
  border: 1px dashed #007849;
  text-decoration: line-through;
  background-color: #00784a57;
}

.task__wrapper.done .task__done-btn{
  border: 1px dashed #007849;
  color: #007849;
}

.task__title {
  margin-top: 0;

  font-family: 'Roboto', sans-serif;
  font-size: 21px;

  max-height: 24px;
  overflow: hidden;
}

.task__text {
  font-family: 'Roboto', sans-serif;
  max-height: 56px;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.task__controls {
  position: absolute;
  bottom: 10px;
}

.task .btn {
  margin: 5px;
}

.task__done-btn {
  left: 50px;
}


/* assistent styles */

.assistant__percent {
  font-size: 56px;
  font-family: 'Roboto', sans-serif;
}

.assistant > p {
  margin: 1px 0;
  font-family: 'Roboto', sans-serif;
  font-size: 21px;
}


/* fix */

.input__task-title {
  margin-top: 10px;
}

/*==================================================
  media queries
==================================================*/

@media not all and (hover: none) {
  /* behaviour for non-touch browsers */
  .btn:hover {
    border: 1px dashed #0375b4;
    color: #0375b4;
  }

  .task__wrapper:hover {
    border: 1px dashed #0375b4;
  }

}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {

  .app {
      padding-bottom: 60px;
  }

  .app__title {
      font-size: 36px;
  }

  .wrapper {
      width: 90%;
  
      flex-direction: column;
  }
  
  .wrapper > .col {
      width: 100%;
  }

}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {

  .app {
      padding-bottom: 60px;
  }

  .app__title {
      font-size: 36px;
  }

  .wrapper {
      width: 90%;
  
      flex-direction: column;
  }
  
  .wrapper > .col {
      width: 100%;
  }

}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) and (max-width: 768px)  {

  .app {
      padding-bottom: 60px;
  }

  .wrapper {
      width: 80%;
  
      flex-direction: column;
  }
  
  .wrapper > .col {
      width: 100%;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) and (max-width: 992px) {

  .app {
      padding-bottom: 60px;
  }

  .wrapper {
      width: 70%;
  
      flex-direction: column;
  }
  
  .wrapper > .col {
      width: 100%;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .wrapper {
      width: 90%;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) and (max-width: 1600px) {

  .wrapper {
      width: 80%;
  }
}

/*==================================================
  animation
==================================================*/

.task.fade-exit {
  animation: fadeHeight 0.5s;
  animation-fill-mode: forwards;
  animation-delay: 0.5s;
}

.task.fade-exit .task__wrapper{
  animation: fadeTransform 0.5s;
  animation-fill-mode: forwards;
}

.task.fade-enter {
  animation: fadeHeight 0.5s;
  animation-fill-mode: both;
  animation-direction: reverse;
}

.task.fade-enter .task__wrapper{
  animation: fadeTransform 0.5s;
  animation-fill-mode: both;
  animation-direction: reverse;
  animation-delay: 0.5s;
}


@keyframes fadeTransform {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(100px);
    opacity: 0;
  }
}

@keyframes fadeHeight {
  0% {
  }
  100% {
    height: 0;
  }
}